<template>
  <v-container fluid grid-list-md>
    <v-layout row>
      <v-flex d-flex xs12 sm6 md2>
        <!-- <v-card color="purple" dark>
          <v-card-title primary class="title">Lorem</v-card-title>
          <v-card-text>{{ lorem }}</v-card-text>
        </v-card>-->
      </v-flex>
      <v-flex d-flex xs12 sm12 md8>
        <v-layout row wrap>
          <v-expansion-panel>
            <v-expansion-panel-content>
              <template v-slot:header>
                <v-card :color="services.rest_proxy.status == 'connected' ? 'green' : 'red'" dark>
                  <v-card-title primary class="title">{{services.rest_proxy.url}}</v-card-title>
                </v-card>
              </template>
              <v-layout wrap align-center px-4>
                <v-flex xs12 sm6 d-flex>
                  <v-text-field v-model="services.rest_proxy.url" label="Rest Service Url" required></v-text-field>
                </v-flex>
                <v-flex xs12 sm2 d-flex>
                  <div>
                    <v-btn color="default" @click="fetch_topics" small>
                      <v-icon>cached</v-icon>
                    </v-btn>
                  </div>
                </v-flex>
              </v-layout>
            </v-expansion-panel-content>
          </v-expansion-panel>
          <v-flex d-flex>
            <v-tabs v-model="active" color="cyan" dark slider-color="yellow">
              <v-tab key="producer" ripple>Producer</v-tab>
              <v-tab key="records" ripple>Record Counts</v-tab>
              <v-tab-item key="producer">
                <v-form ref="form">
                  <v-layout wrap align-center>
                    <v-flex xs12 sm6 d-flex>
                      <v-select
                        v-model="record.topic"
                        :items="cluster.visible_topics"
                        v-validate="'required'"
                        data-vv-name="topic_name"
                        :error-messages="errors.collect('topic_name')"
                        @change="set_topic"
                        label="Topic"
                        required
                      ></v-select>
                    </v-flex>
                    <v-flex xs12 sm3 d-flex>
                      <v-checkbox
                        v-model="cluster.show_internal_topics"
                        @change="apply_topic_filter"
                        label="show internal"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 sm3 d-flex>
                      <v-select
                        v-model="record.partition"
                        :items="available_partitions"
                        label="Partition #"
                      ></v-select>
                    </v-flex>
                    <v-flex xs12 sm12 d-flex>
                      <v-text-field
                        v-model="record.key"
                        v-validate="'required'"
                        data-vv-name="record_key"
                        :error-messages="errors.collect('record_key')"
                        label="Record Key"
                        required
                      ></v-text-field>
                    </v-flex>
                    <v-flex xs12 sm12 d-flex>
                      <v-textarea
                        outline
                        required
                        height="200px"
                        v-validate="'required|jsony'"
                        data-vv-name="record_value"
                        :error-messages="errors.collect('record_value')"
                        label="Record Value"
                        v-model="record.value"
                      ></v-textarea>
                    </v-flex>
                  </v-layout>
                  <div>
                    <v-btn color="default" @click="clear">Reset</v-btn>
                    <v-btn color="success" @click="submit">Send</v-btn>
                  </div>
                </v-form>
              </v-tab-item>
              <v-tab-item key="records">
                <v-form ref="rec_form">
                  <v-layout wrap align-center>
                    <v-flex xs12 sm6 d-flex>
                      <v-select
                        v-model="record.topic"
                        :items="cluster.visible_topics"
                        v-validate="'required'"
                        data-vv-name="topic_name"
                        :error-messages="errors.collect('topic_name')"
                        @change="set_topic"
                        label="Topic"
                        required
                      ></v-select>
                    </v-flex>
                    <v-flex xs12 sm3 d-flex>
                      <v-checkbox
                        v-model="cluster.show_internal_topics"
                        @change="apply_topic_filter"
                        label="show internal"
                      ></v-checkbox>
                    </v-flex>
                    <v-flex xs12 sm12 d-flex>
                      <v-data-table
                        :headers="record_table.headers"
                        :items="record_table.items"
                        class="elevation-1"
                      >
                        <template v-slot:items="props">
                          <td>{{ props.item.name }}</td>
                          <td class="text-xs-right">{{ props.item.total }}</td>
                          <td class="text-xs-left">{{ props.item.week }}</td>
                          <td class="text-xs-left">{{ props.item.day }}</td>
                          <td class="text-xs-left">{{ props.item.hour }}</td>
                          <td class="text-xs-left">{{ props.item.m10 }}</td>
                          <td class="text-xs-left">{{ props.item.m1 }}</td>
                        </template>
                      </v-data-table>
                    </v-flex>
                  </v-layout>
                  <div>
                    <v-btn color="success" @click="count_records">Count Records</v-btn>
                  </div>
                </v-form>
              </v-tab-item>
            </v-tabs>
          </v-flex>
          <v-flex d-flex>
            <v-layout wrap align-center px-5>
              <v-alert
                v-if="report.message"
                :value="true"
                :color="report.color"
                icon="info"
                outline
              >{{report.message}}</v-alert>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import axios from "axios";
import { _ } from "vue-underscore";

let default_record_value = JSON.stringify({ sport: "Mountain Biking" });
let default_record_key = "Bored";

export default {
  $_veeValidate: {
    validator: "new"
  },
  data: () => ({
    active: null,
    services: {
      rest_proxy: { url: "http://35.246.229.124:8082", status: "disconnected" }
    },
    available_partitions: [],
    report: { message: null, color: "red" },
    record: {
      key: default_record_key,
      value: default_record_value,
      partition: null,
      topic: null
    },
    cluster: { topics: [], visible_topics: [], show_internal_topics: true },
    record_table: {
      headers: [
        {
          text: "Partition",
          align: "left",
          // sortable: false,
          value: "name"
        },
        { text: "Total Records", value: "total" }
      ],
      items: []
    },
    dictionary: {
      custom: {
        topic_name: {
          required: "Topic Name is required"
        },
        record_key: {
          required: "Record Key is required"
        },
        record_value: {
          jsony: "Record Value must be a valid JSON",
          required: "Record Value is required"
        }
      }
    }
  }),
  mounted() {
    this.$validator.localize("en", this.dictionary);
    this.fetch_topics();
  },
  methods: {
    set_partition(partition) {
      this.record.partition = partition;
    },
    set_topic(topic_name) {
      axios
        .get(this.services.rest_proxy.url + "/topics/" + this.record.topic)
        .then(response => {
          // JSON responses are automatically parsed.
          console.log(response.data);
          this.available_partitions = _.map(response.data.partitions, function(
            i
          ) {
            return i.partition;
          });
          console.log(this.available_partitions);
        })
        .catch(error => {
          console.log(error.response);
        });
    },
    fetch_topics() {
      axios
        .get(this.services.rest_proxy.url + "/topics")
        .then(response => {
          this.services.rest_proxy.status = "connected";
          // JSON responses are automatically parsed.
          this.cluster.topics = response.data;
          this.cluster.visible_topics = this.cluster.topics.slice();
        })
        .catch(error => {
          console.log(error.response);
        });
    },
    send_record() {
      let records = {
        records: [
          {
            key: this.record.key,
            value: JSON.parse(this.record.value),
            partition: this.record.partition
          }
        ]
      };

      this.report.message = null;
      axios
        .post(
          this.services.rest_proxy.url + "/topics/" + this.record.topic,
          records,
          {
            "Content-Type": "application/vnd.kafka.json.v1+json",
            "s-client-type": "vue"
          }
        )
        .then(response => {
          console.log(response);
          this.report.message = JSON.stringify(response.data);
          this.report.color = "success";
        })
        .catch(error => {
          this.report.message = JSON.stringify(error);
          console.log(error);
          this.report.color = "warning";
        });
    },
    apply_topic_filter() {
      this.cluster.visible_topics = this.cluster.show_internal_topics
        ? this.cluster.topics.slice()
        : _.filter(this.cluster.topics, function(i) {
            return !i.startsWith("_");
          });
      // console.log(this.cluster.topicsVisible)
    },
    submit() {
      let isValid = this.$validator.validateAll().then(valid => {
        if (!valid) {
          // do invalid
        } else {
          this.send_record();
        }
      });
      console.log(isValid);
    },
    clear() {
      this.record.key = null;
      this.record.value = "";
      this.$validator.reset();
    },
    count_records() {
      if (this.record.topic != null) {
        axios
          .get(this.services.rest_proxy.url + "/offsets/" + this.record.topic)
          .then(response => {
            // JSON responses are automatically parsed.
            this.record_table.items = _.map(response.data.offsets, function(
              rlist,
              key
            ) {
              return {
                name: key,
                total: rlist[0]
              };
            });
            console.log(response);
          })
          .catch(error => {
            console.log(error.response);
          });
      }
    }
  }
};
</script>

